import React, { useEffect, useState } from 'react';
import ReactApexChart from 'react-apexcharts';

const NsbPieChart = () => {
    const [datane, setData] = useState([]);
    const [loading, setLoading] = useState(true);
    useEffect(() => {
        try {
            fetch('http://10.65.181.52/sabm/latlogin/nsb/show_json.php')
                .then(response => response.json())
                .then(data => {
                    setData(data.product_name_table.piechart);
                    setLoading(false);
                })
                .catch(error => console.error('Error fetching data:', error));
        } catch (error) {
            console.error('Error fetching data:', error);
            setLoading(false);
        }
    }, []);

    const chartData = {
        series: datane.series,
        options: {
            colors: ["#e60073", "#ff9933", "#999999", "#FF0000", "#000000"],
            chart: {
                type: 'donut',
                margin: 15,
            },
            legend: {
                show: false,
                position: 'top'
            },
            labels: datane.label,
            responsive: [{
                breakpoint: 480,
                legend: {
                    position: 'bottom'
                },
                options: {
                    chart: {
                        width: 200,
                    },
                    plotOptions: {
                        pie: {
                            expandOnClick: true,
                        },
                    }
                }
            }]
        },
    };
    return (
        <>
            {loading ? (
                <p>Loading...</p>
            ) : (
                <ReactApexChart options={chartData.options} series={chartData.series} type="donut" width="100%" />
            )}
        </>

    )
}

export default NsbPieChart