import React, { useEffect, useState } from 'react';
import 'flatpickr/dist/flatpickr.min.css';
import Button from 'react-bootstrap/Button';
import NsbTableRegion from './NsbTableRegion';
import NsbTableArea from './NsbTableArea';
import NsbTableBranch from './NsbTableBranch';
import NsbChartLive from './NsbChartLive';
import NsbChartDaily from './NsbChartDaily';
import NsbChartMtd from './NsbChartMtd';
import NsbPieChart from './NsbPieChart';
import NsbBarChartMonthly from './NsbBarChartMonthly';

function Nsb() {
    const [jsonData, setJsonData] = useState([]);
    const [productnameTable, setProductnameTable] = useState([]);
    const [loading, setLoading] = useState(true);
    const [chartTitle, setChartTitle] = useState("Live Hourly Trend");
    const [chartIndex, setChartIndex] = useState("0");
    const [territoryIndex, setTerrirotryIndex] = useState("0");

    useEffect(() => {
        try {
            fetch('http://10.65.181.52/sabm/latlogin/nsb/show_json.php')
                .then(response => response.json())
                .then(data => {
                    setJsonData(data.card_summary);
                    setProductnameTable(data.product_name_table.tabel);
                    setLoading(false);
                })
                .catch(error => console.error('Error fetching data:', error));
        } catch (error) {
            console.error('Error fetching data:', error);
            setLoading(false);
        }
    }, []);

    const handleLiveTrendClick = () => {
        setLoading(true);
        setChartIndex("1");
        setChartTitle("Live Hourly Trend");
        setLoading(false);
    };

    const handleShowTerritory = (pil) => {
        setLoading(true);
        setTerrirotryIndex(pil);

        setLoading(false);
    };

    const handleDailyTrendClick = () => {
        setLoading(true);
        setChartIndex("2");
        setChartTitle("Daily Trend");
        setLoading(false);
    };

    const handleGrowthTrendClick = () => {
        setLoading(true);
        setChartIndex("3");
        setChartTitle("MTD Trend");
        setLoading(false);
    };

    return (
        <main className="content">
            <div className="container-fluid p-0">
                <h1 className="h3 mb-3">Smart Acquisition</h1>
                <div className="row">
                    <div className="col-xl-6 col-xxl-5 d-flex">
                        <div className="w-100">
                            <div className="row">
                                <div className="col-sm-6">
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="row">
                                                <div className="col mt-0">
                                                    <h3 className="card-title">YESTERDAY</h3>
                                                </div>
                                                <div className="col-auto">
                                                    <div className="stat text-primary bg-light">
                                                        {jsonData.flag_yest === 'danger' ? (
                                                            <i className="fa fa-arrow-down text-danger" style={{ color: "#666" }} />
                                                        ) : (
                                                            <i className="fa fa-arrow-up text-success" style={{ color: "#666" }} />
                                                        )}                                                        
                                                    </div>
                                                </div>
                                            </div>
                                            <h1 className="mt-1 mb-3">
                                                {(Number(jsonData.yesterday_rev) / 1000000000).toFixed(2).toLocaleString()} Bn
                                            </h1>
                                            <div className="mb-0">                                                               
                                                <span className={`badge badge-${jsonData.flag_yest}-light text-${jsonData.flag_yest}`}>
                                                    {jsonData.yesterday_growth}%
                                                </span>
                                                <span className="text-muted"> MoM Growth</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="row">
                                                <div className="col mt-0">
                                                    <h5 className="card-title">QUARTER</h5>
                                                </div>
                                                <div className="col-auto">
                                                    <div className="stat text-primary bg-light">
                                                        {jsonData.quarter_flag === 'danger' ? (
                                                            <i className="fa fa-arrow-down text-danger" style={{ color: "#666" }} />
                                                        ) : (
                                                            <i className="fa fa-arrow-up text-success" style={{ color: "#666" }} />
                                                        )}                                                        
                                                    </div>
                                                </div>
                                            </div>
                                            <h1 className="mt-1 mb-3">{(Number(jsonData.quarter_rev) / 1000000000).toFixed(2).toLocaleString()} Bn</h1>
                                            <div className="mb-0">
                                                <span className={`badge badge-${jsonData.quarter_flag}-light text-${jsonData.quarter_flag}`}>                                                                                                     
                                                    {jsonData.quarter_growth}% </span>
                                                <span className="text-muted"> MoM Growth</span>

                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-sm-6">
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="row">
                                                <div className="col mt-0">
                                                    <h5 className="card-title">THIS MONTH</h5>
                                                </div>
                                                <div className="col-auto">
                                                    <div className="stat text-primary bg-light">
                                                        {jsonData.mom_flag === 'danger' ? (
                                                            <i className="fa fa-arrow-down text-danger" style={{ color: "#666" }} />
                                                        ) : (
                                                            <i className="fa fa-arrow-up text-success" style={{ color: "#666" }} />
                                                        )}
                                                    </div>
                                                </div>
                                            </div>
                                            <h1 className="mt-1 mb-3">{(Number(jsonData.mtd_rev) / 1000000000).toFixed(2).toLocaleString()} Bn</h1>
                                            <div className="mb-0">
                                                <span className={`badge badge-${jsonData.mom_flag}-light text-${jsonData.mom_flag}`}>                                                
                                                    {jsonData.mom_growth}%
                                                </span>
                                                <span className="text-muted"> MoM</span> |&nbsp;

                                                <span className={`badge badge-${jsonData.yoy_flag}-light text-${jsonData.yoy_flag}`}>
                                                    {jsonData.yoy_growth}%
                                                </span>
                                                <span className="text-muted"> YoY</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="row">
                                                <div className="col mt-0">
                                                    <h5 className="card-title">THIS YEAR</h5>
                                                </div>
                                                <div className="col-auto">
                                                    <div className="stat text-primary bg-light">
                                                        {jsonData.ytd_flag === 'danger' ? (
                                                            <i className="fa fa-arrow-down text-danger" style={{ color: "#666" }} />
                                                        ) : (
                                                            <i className="fa fa-arrow-up text-success" style={{ color: "#666" }} />
                                                        )}
                                                    </div>
                                                </div>
                                            </div>
                                            <h1 className="mt-1 mb-3">{(Number(jsonData.ytd_rev) / 1000000000).toFixed(2).toLocaleString()} Bn</h1>
                                            <div className="mb-0">
                                                <span className={`badge badge-${jsonData.ytd_flag}-light text-${jsonData.ytd_flag}`}>
                                                    {jsonData.ytd_growth}% </span>
                                                <span className="text-muted"> MoM Growth</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-xl-6 col-xxl-7">
                        <div className="card flex-fill w-100">
                            <div className="card-header">
                                <div className="card-actions float-end">
                                    <div className="dropdown position-relative">
                                        <Button data-bs-toggle="dropdown" data-bs-display="static" aria-expanded="false" variant="light">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-more-horizontal align-middle">
                                                <circle cx="12" cy="12" r="1"></circle>
                                                <circle cx="19" cy="12" r="1"></circle>
                                                <circle cx="5" cy="12" r="1"></circle>
                                            </svg>
                                        </Button>
                                        <div className="dropdown-menu dropdown-menu-end">
                                            <Button className="dropdown-item text-secondary" onClick={handleLiveTrendClick}>Live Hourly Trend</Button>
                                            <Button className="dropdown-item text-secondary" onClick={handleDailyTrendClick}>Daily Trend</Button>
                                            <Button className="dropdown-item text-secondary" onClick={handleGrowthTrendClick}>Growth Trend</Button>
                                        </div>
                                    </div>
                                </div>
                                <h5 className="card-title mb-0">{chartTitle}</h5>
                            </div>
                            <div className="card-body py-3 pt-0">
                                {
                                    loading ? (
                                        <p>Loading...</p>
                                    ) : (
                                        chartIndex === "1" || chartIndex === "0" ? (
                                            <>
                                                <div className="d-flex flex-column" style={{ position: 'absolute' }}>
                                                    <div className="col mt-0">
                                                        <h3 className="card-title">TODAY</h3>
                                                    </div>
                                                    <h1 className="mt-1 mb-1">
                                                        {(Number(jsonData.today_rev) / 1000000000).toFixed(2).toLocaleString()} Bn
                                                    </h1>
                                                    <div className="mb-0">
                                                        <span className={`badge badge-${jsonData.today_flag}-light text-${jsonData.today_flag}`}>
                                                            {jsonData.today_growth}%
                                                        </span>
                                                        <span className="text-muted"> Growth</span>
                                                    </div>
                                                </div>
                                                <NsbChartLive />
                                            </>
                                        ) : chartIndex === "2" ? (
                                            <NsbChartDaily />
                                        ) : chartIndex === "3" ? (
                                            <NsbChartMtd />
                                        ) : (
                                            <>

                                            </>
                                        )
                                    )}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 col-lg-8 d-flex">
                        <div className="card flex-fill w-100">
                            <div className="card-header">
                                <div className="card-actions float-end">
                                    <div className="dropdown position-relative">
                                        <Button data-bs-toggle="dropdown" data-bs-display="static" aria-expanded="false" variant="light">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-more-horizontal align-middle"><circle cx="12" cy="12" r="1"></circle><circle cx="19" cy="12" r="1"></circle><circle cx="5" cy="12" r="1"></circle></svg>
                                        </Button>
                                        <div className="dropdown-menu dropdown-menu-end">
                                            <Button className="dropdown-item text-secondary" >Action</Button>
                                            <Button className="dropdown-item text-secondary" >Another action</Button>
                                            <Button className="dropdown-item text-secondary" >Something else here</Button>
                                        </div>
                                    </div>
                                </div>
                                <h5 className="card-title mb-0">Summary Performance</h5>
                            </div>
                            <div className="card-body px-4">
                                <NsbBarChartMonthly />
                            </div>
                        </div>
                    </div>
                    <div className="col-12 col-lg-4 d-flex">
                        <div className="card flex-fill w-100">
                            <div className="card-header">
                                <div className="card-actions float-end">
                                    <div className="dropdown position-relative">
                                        <Button data-bs-toggle="dropdown" data-bs-display="static" aria-expanded="false" variant="light">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-more-horizontal align-middle"><circle cx="12" cy="12" r="1"></circle><circle cx="19" cy="12" r="1"></circle><circle cx="5" cy="12" r="1"></circle></svg>
                                        </Button>
                                        <div className="dropdown-menu dropdown-menu-end">
                                            <Button className="dropdown-item text-secondary" >Trx Type</Button>
                                            <Button className="dropdown-item text-secondary" >Stock Type</Button>
                                            <Button className="dropdown-item text-secondary" >Payment Method</Button>
                                        </div>
                                    </div>
                                </div>
                                <h5 className="card-title mb-0">Product Name</h5>
                            </div>
                            <div className="card-body d-flex">
                                <div className="align-self-center w-100">
                                    <div className="py-3">
                                        <div className="chart chart-xs">
                                            <NsbPieChart />
                                        </div>
                                    </div>
                                    <table className="table mb-0">
                                        <tbody>
                                            {
                                                productnameTable.map((item, index) => (
                                                    <tr key={index}>
                                                        <td className="p-1">{item.nama}</td>
                                                        <td className="text-end p-1">{item.revenue} Bn</td>
                                                    </tr>
                                                ))
                                            }
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                <div className="row">
                    <div className="col-12 col-lg-12 col-xxl-12 d-flex">
                        <div className="card flex-fill">
                            <div className="card-header">
                                <div className="card-actions float-end">
                                    <div className="dropdown position-relative">
                                        <Button data-bs-toggle="dropdown" data-bs-display="static" aria-expanded="false" variant="light">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-more-horizontal align-middle"><circle cx="12" cy="12" r="1"></circle><circle cx="19" cy="12" r="1"></circle><circle cx="5" cy="12" r="1"></circle></svg>
                                        </Button>
                                        <div className="dropdown-menu dropdown-menu-end">
                                            <Button className="dropdown-item text-secondary" onClick={() => handleShowTerritory('area')}>Area Level</Button>
                                            <Button className="dropdown-item text-secondary" onClick={() => handleShowTerritory('region')}>Region Level</Button>
                                            <Button className="dropdown-item text-secondary" onClick={() => handleShowTerritory('branch')}>Branch Level</Button>
                                        </div>
                                    </div>
                                </div>
                                <h5 className="card-title mb-0">Territory Performance</h5>
                            </div>

                            <div className="p-3">
                                {
                                    loading ? (
                                        <p>Loading...</p>
                                    ) : (
                                        territoryIndex === "area" || territoryIndex === "0" ? (
                                            <NsbTableArea />
                                        ) : territoryIndex === "region" ? (
                                            <NsbTableRegion regionClick={true} />
                                        ) : territoryIndex === "branch" ? (
                                            <NsbTableBranch />
                                        ) : (
                                            <></>
                                        )
                                    )
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    )

}

export default Nsb