import React, { useEffect, useState } from 'react';
import ReactApexChart from 'react-apexcharts';

const NsbBarChartMonthly = () => {
    const [datane, setData] = useState([]);
    const [labelBar, setLabelBar] = useState([]);
    const [loading, setLoading] = useState(true);
    useEffect(() => {
        try {
            fetch('http://10.65.181.52/sabm/latlogin/nsb/show_json.php')
                .then(response => response.json())
                .then(data => {
                    setData(data.bar_chart.konten);
                    setLabelBar(data.bar_chart.x_axis[0].hasil);
                    setLoading(false);
                })
                .catch(error => console.error('Error fetching data:', error));
        } catch (error) {
            console.error('Error fetching data:', error);
            setLoading(false);
        }
    }, []);

    const chartData = {
        series:
            datane.map(item => ({
                name: item.area_name,
                data: item.hasil,
            })),

        options: {
            grid: {
                show: false
            },
            chart: {
                type: 'bar',
                height: 350,
                stacked: true,
                toolbar: {
                    show: true
                },
                zoom: {
                    enabled: true
                }
            },
            colors: ["#8738AF", "#C92A5E", "#F62025", "#FCB92D"],
            responsive: [{
                breakpoint: 480,
                options: {
                    legend: {
                        position: 'bottom',
                        offsetX: -10,
                        offsetY: 0
                    }
                }
            }],
            plotOptions: {
                bar: {
                    horizontal: false,
                    borderRadius: 1,
                    dataLabels: {
                        total: {
                            enabled: true,
                            style: {
                                fontSize: '11px',
                                fontWeight: 900
                            },
                            formatter: function (val) {
                                return Math.floor(val)
                            }

                        }
                    }
                },
            },
            yaxis: {
                show: false
            },
            xaxis: {
                type: 'text',
                categories: labelBar,
            },
            legend: {
                position: 'top',
                offsetY: -1
            },
            fill: {
                opacity: 1
            }
        },
    };
    return (
        <>
            {loading ? (
                <p>Loading...</p>
            ) : (
                <ReactApexChart
                    options={chartData.options}
                    series={chartData.series}
                    type="bar"
                    width="100%"
                    height="400"
                />
            )}
        </>

    )
}

export default NsbBarChartMonthly