import React, { useEffect, useState, useRef } from 'react';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import Button from 'react-bootstrap/Button';

import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-fixedheader-bs5/css/fixedHeader.bootstrap5.min.css';

// Impor file JavaScript DataTables dan Bootstrap
import $ from 'jquery';
import "datatables.net";
import 'datatables.net-bs5';
import 'datatables.net-buttons-dt';
import 'datatables.net-buttons/js/buttons.html5.min';
import 'datatables.net-buttons/js/buttons.print.min';


import 'datatables.net-fixedheader-bs5';


const NsbTableBranch = () => {
    const [dataTabel, setDataTabel] = useState([]);
    const [startDate, setStartDate] = useState('');
    const [endDate, setEndDate] = useState('');
    const datepickerRef = useRef(null);

    useEffect(() => {
        try {
            fetch('http://10.65.181.52/sabm/latlogin/nsb/show_json.php')
                .then(response => response.json())
                .then(data => {
                    setDataTabel(data.tabel_branch);
                    

                })
                .catch(error => console.error('Error fetching data:', error));
        } catch (error) {
            console.error('Error fetching data:', error);
        }
    }, []);

    useEffect(() => {
        const fp = flatpickr(datepickerRef.current, {
            mode: 'range',
            dateFormat: 'Y-m-d',
            maxDate: 'today',
            minDate: '2023-01-01',
            onChange: (selectedDates) => {
                if (selectedDates.length === 2) {
                    const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
                    const startDate = selectedDates[0].toLocaleDateString('id-ID', options).split('/').reverse().join('-');
                    const endDate = selectedDates[1].toLocaleDateString('id-ID', options).split('/').reverse().join('-');

                    setStartDate(startDate);
                    setEndDate(endDate);
                }
            },
        });

        return () => {
            // Cleanup Flatpickr instance on component unmount
            fp.destroy();
        };
    }, []);

    useEffect(() => {
        const initializeDataTable = () => {
            const table = $('#example').DataTable({
                paging: true,
                searching: true,
                data: dataTabel,
                fixedColumns: {
                    leftColumns: 3,
                    rightColumns: 0, // Atur ke 0 jika tidak ada kolom yang akan di-freeze di sebelah kanan
                },
                dom: 'lBfrtip',
                buttons: [
                    {
                        extend: 'copy',
                        className: 'btn btn-outline-secondary', // Tambahkan kelas CSS untuk warna dan gaya
                        text: '<i class="fas fa-copy"></i> Copy', // Ikona dapat ditambahkan dengan menggunakan HTML
                    },
                    {
                        extend: 'csv',
                        className: 'btn btn-outline-secondary',
                        text: '<i class="fas fa-file-csv"></i> CSV',
                    },
                    {
                        extend: 'excel',
                        className: 'btn btn-outline-secondary',
                        text: '<i class="fas fa-file-excel"></i> Excel',
                    },
                    {
                        extend: 'pdf',
                        className: 'btn btn-outline-secondary',
                        text: '<i class="fas fa-file-pdf"></i> PDF',
                    },
                    {
                        extend: 'print',
                        className: 'btn btn-outline-secondary',
                        text: '<i class="fas fa-print"></i> Print',
                    },
                ],
                columns: [
                    {
                        data: 'area_name',
                        createdCell: function (td) {
                            $(td).css('white-space', 'nowrap');
                        }
                    },
                    {
                        data: 'regional',
                        createdCell: function (td) {
                            $(td).css('white-space', 'nowrap');
                        }
                    },
                    {
                        data: 'branch',
                        createdCell: function (td) {
                            $(td).css('white-space', 'nowrap');
                        }
                    },
                    {
                        data: 'rev_type_ap',
                        render: function (data, type) {
                            if (type === 'display') {
                                return Number(data).toLocaleString();
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            // Tambahkan kelas CSS ke elemen sel
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'rev_type_byu',
                        render: function (data, type) {
                            if (type === 'display') {
                                return Number(data).toLocaleString();
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            // Tambahkan kelas CSS ke elemen sel
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'rev_stock_bulk',
                        render: function (data, type) {
                            if (type === 'display') {
                                return Number(data).toLocaleString();
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            // Tambahkan kelas CSS ke elemen sel
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'rev_stock_bulk_akuisisi',
                        render: function (data, type) {
                            if (type === 'display') {
                                return Number(data).toLocaleString();
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            // Tambahkan kelas CSS ke elemen sel
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'rev_linkaja',
                        render: function (data, type) {
                            if (type === 'display') {
                                return Number(data).toLocaleString();
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            // Tambahkan kelas CSS ke elemen sel
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'rev_bukpin',
                        render: function (data, type) {
                            if (type === 'display') {
                                return Number(data).toLocaleString();
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            // Tambahkan kelas CSS ke elemen sel
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'rev_today',
                        render: function (data, type) {
                            if (type === 'display') {
                                return Number(data).toLocaleString();
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            // Tambahkan kelas CSS ke elemen sel
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'rev_mtd',
                        render: function (data, type) {
                            if (type === 'display') {
                                return Number(data).toLocaleString();
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            // Tambahkan kelas CSS ke elemen sel
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'mom',
                        render: function (data, type, row) {
                            if (type === 'display') {
                                return `<span class=${row.flag_yoy}>${Number(data).toLocaleString()}%</span>`;
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'yoy',
                        render: function (data, type, row) {
                            if (type === 'display') {
                                return `<span class=${row.flag_yoy}>${Number(data).toLocaleString()}%</span>`;
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'star_mtd',
                        render: function (data, type) {
                            if (type === 'display') {
                                return Number(data).toLocaleString();
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            // Tambahkan kelas CSS ke elemen sel
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                    {
                        data: 'cr',
                        render: function (data, type, row) {
                            if (type === 'display') {
                                return `${Number(data).toLocaleString()}%`;
                            }
                            return data;
                        },
                        createdCell: function (td) {
                            $(td).addClass('d-none d-xl-table-cell text-end');
                        }
                    },
                ],
                createdRow: function (row) {
                    $(row).css('font-size', '11px');
                },
            });
            new $.fn.dataTable.FixedHeader(table);
            $('.dt-buttons').css('margin-top', '10px');
            return () => {
                table.destroy();
            };
        };

        if ($.fn.DataTable.isDataTable('#example') && dataTabel.length > 0) {
            const table = $('#example').DataTable();
            table.clear().rows.add(dataTabel).draw();
        } else if (dataTabel.length > 0) {
            initializeDataTable();
        }
    }, [dataTabel]);

    const handleDownload = () => {
        const selectedDate = datepickerRef.current.value;
        if (!selectedDate) {
            alert('Pilih tanggal terlebih dahulu!');
            return;
        } else {
            const formData = new FormData();
            formData.append('startDate', startDate);
            formData.append('endDate', endDate);

            fetch('http://10.65.181.52/sabm/latlogin/nsb/download_csv.php', {
                method: 'POST',
                body: formData,
            })
                .then(response => response.blob())
                .then(blob => {
                    const url = window.URL.createObjectURL(new Blob([blob]));
                    const a = document.createElement('a');
                    a.href = url;
                    a.download = `report_sa_digipos_${startDate}_${endDate}.csv`;
                    document.body.appendChild(a);
                    a.click();
                    document.body.removeChild(a);
                    window.URL.revokeObjectURL(url);
                })
                .catch(error => console.error('Error fetching data:', error));
        }
    };

    const total = dataTabel.reduce((acc, item) => {
        acc.rev_type_ap += +item.rev_type_ap || 0;
        acc.rev_type_arp += +item.rev_type_arp || 0;
        acc.rev_type_byu += +item.rev_type_byu || 0;
        acc.rev_stock_bulk += +item.rev_stock_bulk || 0;
        acc.rev_stock_bulk_akuisisi += +item.rev_stock_bulk_akuisisi || 0;
        acc.rev_linkaja += +item.rev_linkaja || 0;
        acc.rev_bukpin += +item.rev_bukpin || 0;
        acc.rev_mtd += +item.rev_mtd || 0;
        acc.rev_today += +item.rev_today || 0;
        acc.star_mtd += +item.star_mtd || 0;
        acc.rev_mom += +item.rev_mom || 0;
        acc.rev_yoy += +item.rev_yoy || 0;
        return acc;
    }, {
        rev_type_ap: 0,
        rev_type_arp: 0,
        rev_type_byu: 0,
        rev_stock_bulk: 0,
        rev_stock_bulk_akuisisi: 0,
        rev_linkaja: 0,
        rev_mtd: 0,
        rev_today: 0,
        rev_bukpin: 0,
        star_mtd: 0,
        rev_mom: 0,
        rev_yoy: 0
    });


    return (
        <>
       
        
        <div id="tableContainer" className="table-responsive" style={{ overflow: 'auto', maxWidth:'100%'}}>
            
            <table id="example" className="table table-hover table-sm table-striped table-responsive table-bordered">
                <thead>
                    <tr style={{ fontSize: "11px" }}>
                        <th className="text-center">AREA NAME</th>
                        <th className="text-center">REGIONAL</th>
                        <th className="text-center">BRANCH</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">AP / ARP</th>
                        <th className="d-xl-table-cell text-center pr-3">BYU</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">BULK</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">AKUISISI</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">LINKAJA</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">BUKPIN</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">TODAY</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">MTD</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">MOM</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">YOY</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">POINT</th>
                        <th className="d-none d-xl-table-cell text-center pr-3">C.R</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr style={{ fontSize: "11px" }}>
                        <th className="text-end"><strong>TOTAL</strong></th>
                        <th></th>
                        <th></th>
                        <th className="d-none d-xl-table-cell text-end">{((Number(total.rev_type_ap) + Number(total.rev_type_arp))).toLocaleString()}</th>
                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_type_byu).toLocaleString()}</th>
                        <th className="d-xl-table-cell text-end">{Number(total.rev_stock_bulk).toLocaleString()}</th>
                        <th className="d-xl-table-cell text-end">{Number(total.rev_stock_bulk_akuisisi).toLocaleString()}</th>
                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_linkaja).toLocaleString()}</th>
                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_bukpin).toLocaleString()}</th>
                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_today).toLocaleString()}</th>
                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_mtd).toLocaleString()}</th>
                        <th className="d-none d-xl-table-cell text-end" style={{ color: (Number(total.rev_mtd) > Number(total.rev_mom)) ? 'green' : 'red' }}>
                            {
                                (((Number(total.rev_mtd) / Number(total.rev_mom)) - 1) * 100).toFixed(2).toLocaleString()
                            }%
                        </th>
                        <th className="d-none d-xl-table-cell text-end" style={{ color: (Number(total.rev_mtd) > Number(total.rev_yoy)) ? 'green' : 'red' }}>
                            {
                                (((Number(total.rev_mtd) / Number(total.rev_yoy)) - 1) * 100).toFixed(2).toLocaleString()
                            }%
                        </th>
                        <th className="d-none d-xl-table-cell text-end">{Number(total.star_mtd).toLocaleString()}</th>
                        <th className="d-none d-xl-table-cell text-end">
                            {
                                (Number(total.star_mtd) / Number(total.rev_mtd) * 100).toFixed(2).toLocaleString()
                            }%
                        </th>
                    </tr>
                </tfoot>
            </table>
            <hr />
            <div className="py-1 d-flex justify-content-end">
                <Form>
                    <Row>
                        <Col>
                            <Form.Control type="text" style={{ width: '220px' }} placeholder="Pilih Tanggal" className="form-control" ref={datepickerRef} />
                        </Col>
                        <Col>
                            <Button variant="danger" onClick={handleDownload}>Download Outlet Id Level</Button>
                        </Col>
                    </Row>
                </Form>
            </div>

        </div>
        </>
    )
}

export default NsbTableBranch