import React, { useEffect, useState } from 'react';
import NsbTableClusterSelected from './NsbTableClusterSelected';
import Button from 'react-bootstrap/Button';

const NsbTableBranchSelected = ({ selectedRegion }) => {
    const [dataTabel, setDataTabel] = useState([]);
    const [loading, setLoading] = useState(true);
    const [selectedBranch, setSelectedBranch] = useState(null);
    const [clusterFlag, setClusterFlag] = useState("0");
    const [total, setTotal] = useState({
        rev_type_ap: 0,
        rev_type_arp: 0,
        rev_type_byu: 0,
        rev_stock_bulk: 0,
        rev_stock_bulk_akuisisi: 0,
        rev_linkaja: 0,
        rev_bukpin: 0,
        rev_mtd: 0,
        rev_today: 0,
        star_mtd: 0,
        rev_mom: 0,
        rev_yoy: 0
    });


    useEffect(() => {
        try {
            fetch('http://10.65.181.52/sabm/latlogin/nsb/show_json.php')
                .then(response => response.json())
                .then(data => {
                    setDataTabel(data.tabel_branch);
                    setLoading(false);
                })
                .catch(error => console.error('Error fetching data:', error));
        } catch (error) {
            console.error('Error fetching data:', error);
            setLoading(false);
        }
    }, []);

    const handleBranchClick = (branchValue) => {
        setSelectedBranch(branchValue);
        setClusterFlag("1");
    }

    const handleBackToBranch = () => {
        setClusterFlag("0");
    }

    useEffect(() => {
        // Hitung total untuk data yang sesuai dengan selectedRegion
        const regionTotal = dataTabel.reduce((acc, item) => {
            if (item.regional === selectedRegion) {
                acc.rev_type_ap += +item.rev_type_ap || 0;
                acc.rev_type_arp += +item.rev_type_arp || 0;
                acc.rev_type_byu += +item.rev_type_byu || 0;
                acc.rev_stock_bulk += +item.rev_stock_bulk || 0;
                acc.rev_stock_bulk_akuisisi += +item.rev_stock_bulk_akuisisi || 0;
                acc.rev_linkaja += +item.rev_linkaja || 0;
                acc.rev_bukpin += +item.rev_bukpin || 0;
                acc.rev_mtd += +item.rev_mtd || 0;
                acc.rev_today += +item.rev_today || 0;
                acc.star_mtd += +item.star_mtd || 0;
                acc.rev_mom += +item.rev_mom || 0;
                acc.rev_yoy += +item.rev_yoy || 0;
            }
            return acc;
        }, {
            rev_type_ap: 0,
            rev_type_arp: 0,
            rev_type_byu: 0,
            rev_stock_bulk: 0,
            rev_stock_bulk_akuisisi: 0,
            rev_linkaja: 0,
            rev_bukpin: 0,
            rev_mtd: 0,
            rev_today: 0,
            star_mtd: 0,
            rev_mom: 0,
            rev_yoy: 0
        });
        setTotal(regionTotal);
    }, [selectedRegion, dataTabel]);

    return (
        <>
            {
                loading ? (
                    <p>Loading...</p>
                ) : (
                    clusterFlag === "0" ? (
                        <>
                        <div className="table-responsive">
                            <table className="table table-hover table-sm table-striped table-responsive table-bordered">
                                <thead>
                                    <tr style={{ fontSize: "11px" }}>
                                        <th className="text-center">AREA NAME</th>
                                        <th className="text-center">REGIONAL</th>
                                        <th className="text-center">BRANCH</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">AP / ARP</th>
                                        <th className="d-xl-table-cell text-center pr-3">BYU</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">BULK</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">AKUISISI</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">LINKAJA</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">BUKPIN</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">TODAY</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">MTD</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">MOM</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">YOY</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">POINT</th>
                                        <th className="d-none d-xl-table-cell text-center pr-3">C.R</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        dataTabel
                                            .filter((item) => item.regional.includes(selectedRegion))
                                            .map((item, index) => (
                                                <tr key={index} style={{ fontSize: "11px" }}>
                                                    <td style={{ textWrap: 'nowrap' }}>{item.area_name}</td>
                                                    <td style={{ textWrap: 'nowrap' }}>{item.regional}</td>
                                                    <td style={{ textWrap: 'nowrap', cursor: 'pointer' }} onClick={() => handleBranchClick(item.branch)}>{item.branch}</td>
                                                    <td className="d-none d-xl-table-cell text-end">{(Number(item.rev_type_ap) + Number(item.rev_type_arp)).toLocaleString()}</td>
                                                    <td className="d-xl-table-cell text-end">{Number(item.rev_type_byu).toLocaleString()}</td>
                                                    <td className="d-xl-table-cell text-end">{Number(item.rev_stock_bulk).toLocaleString()}</td>
                                                    <td className="d-none d-xl-table-cell text-end">{Number(item.rev_stock_bulk_akuisisi).toLocaleString()}</td>
                                                    <td className="d-none d-xl-table-cell text-end">{Number(item.rev_linkaja).toLocaleString()}</td>
                                                    <td className="d-xl-table-cell text-end">{Number(item.rev_bukpin).toLocaleString()}</td>
                                                    <td className="d-none d-xl-table-cell text-end">{Number(item.rev_today).toLocaleString()}</td>
                                                    <td className="d-none d-xl-table-cell text-end">{Number(item.rev_mtd).toLocaleString()}</td>
                                                    <td className="d-none d-xl-table-cell text-end"><span className={item.flag_mom}>{Number(item.mom).toLocaleString()}%</span></td>
                                                    <td className="d-none d-xl-table-cell text-end"><span className={item.flag_yoy}>{Number(item.yoy).toLocaleString()}%</span></td>
                                                    <td className="d-none d-xl-table-cell text-end">{Number(item.star_mtd).toLocaleString()}</td>
                                                    <td className="d-none d-xl-table-cell text-end">{Number(item.cr).toLocaleString()}%</td>
                                                </tr>
                                            ))
                                    }
                                </tbody>
                                <tfoot>
                                    <tr style={{ fontSize: "11px" }}>
                                        <th colSpan={3} className="text-end"><strong>TOTAL</strong></th>
                                        <th className="d-none d-xl-table-cell text-end">{((Number(total.rev_type_ap) + Number(total.rev_type_arp))).toLocaleString()}</th>
                                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_type_byu).toLocaleString()}</th>
                                        <th className="d-xl-table-cell text-end">{Number(total.rev_stock_bulk).toLocaleString()}</th>
                                        <th className="d-xl-table-cell text-end">{Number(total.rev_stock_bulk_akuisisi).toLocaleString()}</th>
                                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_linkaja).toLocaleString()}</th>
                                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_bukpin).toLocaleString()}</th>
                                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_today).toLocaleString()}</th>
                                        <th className="d-none d-xl-table-cell text-end">{Number(total.rev_mtd).toLocaleString()}</th>
                                        <th className="d-none d-xl-table-cell text-end" style={{ color: (Number(total.rev_mtd) > Number(total.rev_mom)) ? 'green' : 'red' }}>
                                            {
                                                (((Number(total.rev_mtd) / Number(total.rev_mom)) - 1) * 100).toFixed(2).toLocaleString()
                                            }%
                                        </th>
                                        <th className="d-none d-xl-table-cell text-end" style={{ color: (Number(total.rev_mtd) > Number(total.rev_yoy)) ? 'green' : 'red' }}>
                                            {
                                                (((Number(total.rev_mtd) / Number(total.rev_yoy)) - 1) * 100).toFixed(2).toLocaleString()
                                            }%
                                        </th>
                                        <th className="d-none d-xl-table-cell text-end">{Number(total.star_mtd).toLocaleString()}</th>
                                        <th className="d-none d-xl-table-cell text-end">
                                            {
                                                (Number(total.star_mtd) / Number(total.rev_mtd) * 100).toFixed(2).toLocaleString()
                                            }%
                                        </th>
                                    </tr>
                                </tfoot>
                            </table>
                            
                            <h5 className="text-danger">Klik nama Branch masing-masing untuk masuk ke level Cluster dan City</h5>
                            <br />
                            <hr />
                        </div>
                        </>
                    ) : (
                        <>
                            <NsbTableClusterSelected selectedBranch={selectedBranch} />
                            <Button variant="danger" onClick={handleBackToBranch}>Back to Branch</Button>
                        </>
                    )
                )
            }
        </>
    )
}

export default NsbTableBranchSelected