import React, { useEffect, useState } from 'react';
import { Link } from "react-router-dom";

function SideNav({ isCollapsed  }) {
    const [pbiData, setPbiData] = useState([]); 
    const [loading, setLoading] = useState(true);
    let style;
    if (!sessionStorage.getItem("uname")) {
        style = isCollapsed ? "sidebar js-sidebar" : "sidebar js-sidebar collapsed";
    }else{
        style = isCollapsed ? "sidebar js-sidebar collapsed" : "sidebar js-sidebar";
    }
    
    useEffect(() => {
        try {
            fetch('http://10.65.181.52/sabm/latlogin/powerbi.php')
                .then(response => response.json())
                .then(data => {
                    setPbiData(data);
                    setLoading(false);
                })
                .catch(error => console.error('Error fetching data:', error));
        } catch (error) {
            console.error('Error fetching data:', error);
            setLoading(false);
        }
    }, []);

    const handleLogout = async () => {
        try {
            sessionStorage.removeItem("uname");
            //window.location.reload();
            //navigate("/login");
            //window.location.href = `${process.env.REACT_APP_PATH_URL}/login`;
            //navigate(`${process.env.REACT_APP_PATH_URL}/`);
            if (!sessionStorage.getItem("uname")) {
                //alert("Anda sedang")
                window.location.href = `${process.env.REACT_APP_PATH_SERVER}`;
                //navigate("/login");
              } else {
                //navigate("/homepage");
                window.location.href = `/${process.env.REACT_APP_PATH_SERVER}/homepage`;
              }
        } catch (error) {
            console.error("Terjadi kesalahan:", error);
        }
    };
    return (        
        <nav id="sidebar" className={style} style={{ background: 'linear-gradient(180deg, rgba(134,57,176,1) 0%, rgba(198,42,98,1) 29%, rgba(253,31,30,1) 60%, rgba(253,98,30,1) 86%)' }}>
            <div className="sidebar-content js-simplebar" style={{ backgroundColor: 'rgba(255, 0, 0, 0.0)' }}>
                <Link className="sidebar-brand" to={`${process.env.REACT_APP_PATH_SERVER}`}>
                    <span className="sidebar-brand-text align-middle">
                        Sales Platform
                        <sup><small className="badge bg-danger text-uppercase">Pro</small></sup>
                    </span>
                    <svg className="sidebar-brand-icon align-middle" width="32px" height="32px" viewBox="0 0 24 24" fill="none" stroke="#FFFFFF" strokeWidth="1.5" strokeLinecap="square" strokeLinejoin="miter" color="#FFFFFF" style={{ marginLeft: '-3px' }}>
                        <path d="M12 4L20 8.00004L12 12L4 8.00004L12 4Z" />
                        <path d="M20 12L12 16L4 12" />
                        <path d="M20 16L12 20L4 16" />
                    </svg>
                </Link>
                <ul className="sidebar-nav">
                    <li className="sidebar-header">
                        Main Menu
                    </li>
                    <li className="sidebar-item">
                        <Link data-bs-target="#dashboards" data-bs-toggle="collapse" className="sidebar-link" style={{ background: 'none'}}>
                            <i className="align-middle" data-feather="sliders" /> <span className="align-middle">Digipos</span>
                        </Link>
                        <ul id="dashboards" className="sidebar-dropdown list-unstyled collapse show" data-bs-parent="#sidebar">
                            <li className="sidebar-item"><Link className="sidebar-link" to={`${process.env.REACT_APP_PATH_SERVER}/nsb`}>Smart Acquisition</Link></li>
                            {
                                /*
                            <li className="sidebar-item"><a className="sidebar-link" href="/dashboard-ecommerce">Package Activation <span className="sidebar-badge badge bg-primary">Pro</span></a></li>
                            <li className="sidebar-item"><a className="sidebar-link" href="/dashboard-crypto">Recharge Reguler</a></li>
                                */
                            }
                        </ul>
                    </li>
                    <li className="sidebar-item">
                        <Link style={{ background: 'none'}} className="sidebar-link" to={`${process.env.REACT_APP_PATH_SERVER}/games`}>
                            <i className="align-middle" data-feather="user" /> <span className="align-middle">Games Performance</span>
                        </Link>
                    </li>
                    {
                    /*
                    <li className="sidebar-item">
                        <a className="sidebar-link" href="/pages-profile" style={{ background: 'none'}}>
                            <i className="align-middle" data-feather="user" /> <span className="align-middle">Profile</span>
                        </a>
                    </li>
                    <li className="sidebar-item">
                        <a className="sidebar-link" href="/pages-invoice" style={{ background: 'none'}}>
                            <i className="align-middle" data-feather="credit-card" /> <span className="align-middle">Invoice</span>
                        </a>
                    </li>
                    */
                    }

                    <li className="sidebar-header">
                        Power Tools
                    </li>
                    <li className="sidebar-item">
                        <Link data-bs-target="#forms" data-bs-toggle="collapse" className="sidebar-link collapsed" style={{ background: 'none'}}>
                            <i className="align-middle" data-feather="check-circle" /> <span className="align-middle">Power BI</span>
                        </Link>
                        <ul id="forms" className="sidebar-dropdown list-unstyled collapse " data-bs-parent="#sidebar">                            
                            {
                                loading ? (
                                    <p>Loading...</p>
                                ) : (
                                    pbiData.map((item, index) => (
                                        <li className="sidebar-item" key={index} >
                                            <Link to={`https://app.powerbi.com/reportEmbed?reportId=${item.report_id}&autoAuth=true&ctid=${item.ctid}`} className="sidebar-link" target="_blank" >
                                                {item.menu_name}
                                            </Link>
                                        </li>    
                                    ))
                                )
                            }
                            {
                                /*
                                <li className="sidebar-item"><Link className="sidebar-link" to={`/revenue-prepaid`}>Revenue Prepaid</Link></li>
                                <li className="sidebar-item"><Link className="sidebar-link" to={`/fbshare`}>FB Share</Link></li>                                
                                */
                            }
                        </ul>
                    </li>
                    
                    {
                    /*
                    <li className="sidebar-header">
                        Plugins &amp; Addons
                    </li>
                    
                    
                    <li className="sidebar-item">
                        <a data-bs-target="#multi" data-bs-toggle="collapse" className="sidebar-link collapsed" style={{ background: 'none'}}>
                            <i className="align-middle" data-feather="corner-right-down" /> <span className="align-middle">Multi Level</span>
                        </a>
                        <ul id="multi" className="sidebar-dropdown list-unstyled collapse" data-bs-parent="#sidebar">
                            <li className="sidebar-item">
                                <a data-bs-target="#multi-2" data-bs-toggle="collapse" className="sidebar-link collapsed">Two Levels</a>
                                <ul id="multi-2" className="sidebar-dropdown list-unstyled collapse">
                                    <li className="sidebar-item">
                                        <a className="sidebar-link" href="#">Item 1</a>
                                    </li>
                                    <li className="sidebar-item">
                                        <a className="sidebar-link" href="#">Item 2</a>
                                    </li>
                                </ul>
                            </li>
                            <li className="sidebar-item">
                                <a data-bs-target="#multi-3" data-bs-toggle="collapse" className="sidebar-link collapsed">Three Levels</a>
                                <ul id="multi-3" className="sidebar-dropdown list-unstyled collapse">
                                    <li className="sidebar-item">
                                        <a data-bs-target="#multi-3-1" data-bs-toggle="collapse" className="sidebar-link collapsed">Item 1</a>
                                        <ul id="multi-3-1" className="sidebar-dropdown list-unstyled collapse">
                                            <li className="sidebar-item">
                                                <a className="sidebar-link" href="#">Item 1</a>
                                            </li>
                                            <li className="sidebar-item">
                                                <a className="sidebar-link" href="#">Item 2</a>
                                            </li>
                                        </ul>
                                    </li>
                                    <li className="sidebar-item">
                                        <a className="sidebar-link" href="#">Item 2</a>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </li>
                        */
                    }
                </ul>
                <div className="sidebar-cta">
                    <div className="sidebar-cta-content">
                        { /*
                        <strong className="d-inline-block mb-2">ssdfsdf</strong>
                        <div className="mb-3 text-sm">
                            Keluar dari dashborad klik
                        </div>
                        */ }
                        <div className="d-grid">
                            <Link className="btn btn-outline-primary" onClick={handleLogout}>Logout</Link>
                        </div>
                    </div>
                </div>
            </div>
        </nav>

    )
}

export default SideNav