import "./App.css";
import React, { useState } from 'react';
import { Routes, Route } from "react-router-dom";
import Login from './components/Login';
import Dashboard from './components/Dashboard';
import Header from './components/Header';
import SideNav from './components/SideNav';
import Footer from './components/Footer';
import Nsb from './components/digipos/nsb/Nsb';
import Games from './components/dls/games/Games';
import FbShare from "./components/powerbi/FbShare";
import RevenuePrepaid from "./components/powerbi/RevenuePrepaid";
import BroadbandPorto from "./components/powerbi/BroadbandPorto";
import DigitalPorto from "./components/powerbi/DigitalPorto";
import DriverPerLos from "./components/powerbi/DriverPerLos";
import Newsales from "./components/powerbi/Newsales";
import Recharge from "./components/powerbi/Recharge";
import Indihome from "./components/powerbi/Indihome";
import SelloutPayload from "./components/powerbi/SelloutPayload";

function App() {
  const [isCollapsed, setIsCollapsed] = useState(false);

  // Fungsi untuk menoggle collapse sidebar
  const toggleSidebar = () => {
    setIsCollapsed(!isCollapsed);
  };

  return (
    <div className="wrapper">


      {sessionStorage.getItem("uname") && <SideNav isCollapsed={isCollapsed} />}

      <div className="main">
        {sessionStorage.getItem("uname") && <Header toggleSidebar={toggleSidebar} />}

        <Routes basename={`/${process.env.REACT_APP_PATH_URL}`}>
          <Route index path="/" element={<Login />} />
          { /*<Route path={`${process.env.REACT_APP_PATH_URL}/login`} element={<Login />} /> */}
          <Route path="/login" element={<Login />} />
          <Route path="/homepage" element={<Dashboard />} />
          <Route path="/nsb" element={sessionStorage.getItem("uname") && <Nsb />} />
          <Route path="/games" element={sessionStorage.getItem("uname") && <Games />} />
          <Route path="/fbshare" element={<FbShare />} />
          <Route path="/revenue-prepaid" element={<RevenuePrepaid />} />
          <Route path="/broadband-porto" element={<BroadbandPorto />} />
          <Route path="/digital-porto" element={<DigitalPorto />} />
          <Route path="/driver-per-los" element={<DriverPerLos />} />
          <Route path="/newsales" element={<Newsales />} />
          <Route path="/recharge" element={<Recharge />} />
          <Route path="/indihome" element={<Indihome />} />
          <Route path="/sellout-payload" element={<SelloutPayload />} />

        </Routes>
        {sessionStorage.getItem("uname") && <Footer toggleSidebar={toggleSidebar} />}
      </div>
    </div>
  );
}

export default App;
