import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";


export default function Dashboard() {

    const navigate = useNavigate();
    useEffect(() => {
        // Memeriksa apakah sesi NIK telah aktif
        const isLoggedIn = sessionStorage.getItem("uname");

        // Jika sesi tidak aktif, arahkan pengguna ke halaman login
        if (!isLoggedIn) {
            navigate(`/${process.env.REACT_APP_PATH_URL}`);
        }

    }, [navigate]);

    return (
        <main className="content">
            <div className="container-fluid p-0">
                <div className="row mb-2">
                    <div className="col-sm-6">
                        <h1 className="m-0">Dashboard</h1>
                    </div>{/* /.col */}
                    <div className="col-sm-6">
                        <ol className="breadcrumb float-sm-right">
                            <li className="breadcrumb-item"><a href={`/${process.env.REACT_APP_PATH_URL}`}>Home</a></li>
                            <li className="breadcrumb-item active">Dashboard v1</li>
                        </ol>
                    </div>{/* /.col */}
                </div>{/* /.row */}
            </div>{/* /.container-fluid */}

            {/* /.content-header */}
            {/* Main content */}
            <section className="content">
                <div className="container-fluid">
                    {/* Main row */}
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="card">

                            </div>
                        </div>
                        {/* /.col-md-6 */}
                    </div>

                    {/* /.row (main row) */}
                </div>{/* /.container-fluid */}
            </section>
            {/* /.content */}
        </main>



    )
}