import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { useNavigate } from "react-router-dom";
import '../Login.css';
import Alert from 'react-bootstrap/Alert';
import Modal from 'react-bootstrap/Modal';


function Login() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    // Memeriksa apakah sesi NIK telah aktif
    const isLoggedIn = sessionStorage.getItem("uname");

    // Jika sesi aktif, arahkan pengguna ke halaman Dashboard
    if (isLoggedIn) {
      //navigate(`${process.env.REACT_APP_PATH_URL}/build/homepage`);
      navigate("/homepage");
    } else {
      navigate("/");
    }
  }, [navigate]);


  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      //const response = await fetch(`${process.env.REACT_APP_API_URL}/login.php`, {
      //const response = await fetch("http://10.65.181.52/sabm/latlogin/login.php", {
      //const response = await fetch("http://localhost/latlogin/login.php", {
      const response = await fetch("http://10.65.181.52/sabm/latlogin/login.php", {
      //const response = await fetch(`${process.env.REACT_APP_PATH_FULL}/login.php`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ username, password }),
      });

      const data = await response.json();

      if (data.success) {
        // Set session jika login berhasil
        sessionStorage.removeItem("uname");
        sessionStorage.setItem("uname", data.username);
        sessionStorage.setItem("nik", data.nik);
        sessionStorage.setItem("area", data.area);
        sessionStorage.setItem("level", data.level);
        //navigate(`${process.env.REACT_APP_PATH_URL}/homepage`);
        //window.location.reload();
        //window.location.href = `${process.env.REACT_APP_PATH_URL}/homepage`;
        //window.location.href = "http://localhost/build/homepage";
        //window.location.href = '/dashboard';

        if (!sessionStorage.getItem("uname")) {
          //window.location.href = `${process.env.REACT_APP_PATH_SERVER}/login`;
          //navigate("/login");
          navigate(`/${process.env.REACT_APP_PATH_URL}/login`);
        } else {
          //navigate("/homepage");
          //window.location.href = `${process.env.REACT_APP_PATH_SERVER}/homepage`;
          //window.location.href = `/${process.env.REACT_APP_PATH_URL}/homepage`;
          window.location.href = `/${process.env.REACT_APP_PATH_URL}`;
        }
      } else {
        setError(data.message);
      }
    } catch (error) {
      console.error("Terjadi kesalahan:", error);
    }
  };


  const [show, setShow] = useState(true);
  const handleClose = () => setShow(false);

  return (
    <>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Perhatian</Modal.Title>
        </Modal.Header>
        <Modal.Body>Untuk tampilan yang lebih baik atur zoom browser menjadi 90%</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>

      <Container className="vh-100 d-flex justify-content-center align-items-center">
        <Row style={{ display: "flex", alignItems: "center" }}>
          <Col
            xs={12}
            md={7}
            className="text-center justify-content-center p-100"
            style={{ padding: "30px" }}
          >
            <img
              src="http://10.65.181.52/sabm/login_icon.png"
              alt="login-logo"
              style={{ width: "80%", height: "auto", paddingTop: "50px" }}
            />
          </Col>
          <Col
            xs={12}
            md={5}
            className="justify-content-top"
            style={{ padding: "30px" }}
          >
            <h1 style={{ color: "#BD0951" }} className="mb-5"><strong>Login Page Dashboard</strong></h1>
            {error && (
              <Alert variant="danger" style={{ padding: "10px", fontWeight: "bold" }}>
                {error}
              </Alert>
            )}
            <Form onSubmit={handleSubmit}>
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>
                  <span style={{ fontWeight: "bold", color: "#999" }}>Username</span>
                </Form.Label>
                <Form.Control
                  type="text"
                  name="username"
                  placeholder="contoh: erlangins"
                  onChange={(e) => setUsername(e.target.value)}
                  style={{ padding: "12px", fontSize: "18px" }}
                />
              </Form.Group>

              <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label>
                  <span style={{ fontWeight: "bold", color: "#999" }}>
                    Password
                  </span>
                </Form.Label>
                <Form.Control
                  type="password"
                  placeholder="******"
                  onChange={(e) => setPassword(e.target.value)}
                  style={{ padding: "12px", fontSize: "18px" }}
                />
              </Form.Group>
              <div style={{ float: "right", width: "100%" }}>

                <Button
                  type="button"
                  className="custom-button"
                  onClick={(e) => handleSubmit(e)}
                >
                  Login Dashboard
                </Button>
                <p
                  style={{
                    marginTop: "20px",
                    fontSize: "18px",
                    color: "#999",
                  }}
                >
                  Jika belum memiliki akun login atau lupa password silahkan untuk
                  menghubungi tim CBPA HQ
                  <br /><br />
                  <br /><br />
                </p>

              </div>
            </Form>
          </Col>
        </Row>
      </Container>
    </>

  );
}

export default Login;
