/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { useNavigate } from 'react-router-dom';

function Header({ toggleSidebar  }) {
    
    const nama = sessionStorage.getItem("uname");
    const navigate = useNavigate();
    const handleLogout = async () => {
        try {            
            sessionStorage.removeItem("uname");
            if (!sessionStorage.getItem("uname")) {
                window.location.href = `${process.env.REACT_APP_PATH_SERVER}`;
            } else {
                navigate("/homepage");
            }

            //navigate(`${process.env.REACT_APP_PATH_URL}/`);
        } catch (error) {
            console.error("Terjadi kesalahan:", error);
        }
    };

    return (
        <nav className="navbar navbar-expand navbar-light navbar-bg">
            <a className="sidebar-toggle js-sidebar-toggle" onClick={toggleSidebar}>
                <i className="hamburger align-self-center" />
            </a>
            <h1 className="h3 mt-2"><strong>Klak-Klik</strong> Dashboard</h1>
            <div className="navbar-collapse collapse">
                <ul className="navbar-nav navbar-align">
                    <li className="nav-item dropdown">
                        <a className="nav-icon dropdown-toggle d-inline-block d-sm-none" href="#" data-bs-toggle="dropdown">
                            <i className="align-middle" data-feather="settings" />
                        </a>
                        <a className="nav-link dropdown-toggle d-none d-sm-inline-block" href="#" data-bs-toggle="dropdown">
                            <img src="img/avatars/avatar.jpg" className="avatar img-fluid rounded me-1" alt="Charles Hall" /> <span className="text-dark">{nama}</span>
                        </a>
                        <div className="dropdown-menu dropdown-menu-end">
                            <a className="dropdown-item" href="pages-profile.html"><i className="align-middle me-1" data-feather="user" /> Profile</a>
                            <a className="dropdown-item" href="index.html"><i className="align-middle me-1" data-feather="settings" /> Ganti Password</a>
                            <div className="dropdown-divider" />
                            <a className="dropdown-item" onClick={handleLogout}>Log out</a>
                        </div>
                    </li>
                </ul>
            </div>
        </nav>

    )
}

export default Header